import { Component, OnInit } from '@angular/core';
import { StudentService } from 'src/app/services/student.service';
import { Student } from 'src/app/models/student.model';
import {  FormBuilder, FormGroup, Validators,FormControl  } from '@angular/forms';
import { ActivatedRoute,Router} from '@angular/router';


@Component({
  selector: 'app-student-form',
  templateUrl: './student-form.component.html',
  styleUrls: ['./student-form.component.css']
})
export class StudentFormComponent implements OnInit {

  studentType:string;
   student:Student;
   registrationForm: FormGroup;

  docTypes:any[];
  
   tempDocs:any[]=[,{
    "name":"Policeclearance",
    "requiredFor":{
      "international":true,
      "domestic" :false
    }
  },
  {
    "name":"Passport",
    "requiredFor":{
      "international":true,
      "domestic" :false
    }
  },];
  constructor(private studentService:StudentService,private formBuilder: FormBuilder,private router:Router) { 
    this.registrationForm = this.createFormGroup();    
  }

  createFormGroup() {
    return this.formBuilder.group({
      studentName: new FormControl('',Validators.required),
        category: new FormControl('',Validators.required),
        domicile: new FormControl('',Validators.required),
        birthcertificate: new FormControl('',Validators.required),
        previousmarksheets: new FormControl('',Validators.required),
        signeddeclaration: new FormControl('',Validators.required),
        dob: new FormControl(''),
        fathersName: new FormControl(''),
        mothersName: new FormControl(''),
        lastClassScore: new FormControl(''),
        policeclearance: new FormControl(''),
        passport: new FormControl('')
    });
  }

  ngOnInit():void {
    this.studentService.getDocumentTypes().subscribe(
      docs => {this.docTypes=docs;
      console.log(this.docTypes);  
      }
    );
  }
  onSubmit(){
    console.log(this.registrationForm);
    if(this.registrationForm.valid){      
      let studentName = this.registrationForm.controls['studentName'].value;
      let category = this.registrationForm.controls['category'].value;
      let  domicile = this.registrationForm.controls['domicile'].value;
      let  birthcertificate= this.registrationForm.controls['birthcertificate'].value;
      let previousmarksheets = this.registrationForm.controls['previousmarksheets'].value;
      let signeddeclaration = this.registrationForm.controls['signeddeclaration'].value;
      let dob = this.registrationForm.controls['dob'].value;
      let fathersName = this.registrationForm.controls['fathersName'].value;
      let  mothersName= this.registrationForm.controls['mothersName'].value;
      let  lastClassScore= this.registrationForm.controls['lastClassScore'].value;
      let  passport= this.registrationForm.controls['passport'].value;
      let  policeClearance= this.registrationForm.controls['policeclearance'].value;

      let newId:number =students[students.length-1].id +1;

      let student = new Student(newId,studentName,category,domicile,birthcertificate,previousmarksheets,signeddeclaration,dob,fathersName,mothersName,lastClassScore);

      var students:any[];
      if (localStorage.getItem("Students") === null || localStorage.getItem("Students").length === 2) {
        localStorage.setItem("Students", JSON.stringify(students));
      }
      else{
        students= JSON.parse(localStorage.getItem("Students"));      
      }
      students.push(student);
      localStorage.setItem("Students", JSON.stringify(students));
    }
    this.router.navigate(['/studentslist']);
  }

}
