import { Component, OnInit } from '@angular/core';

import {StudentService} from '../services/student.service';

@Component({
  selector: 'app-students-list',
  templateUrl: './students-list.component.html',
  styleUrls: ['./students-list.component.css']
})
export class StudentsListComponent implements OnInit {

  filterRecords(filterBy: string): any[] {
   filterBy = filterBy.toLowerCase();
   return this.students.filter((student:any) =>student.name.toLowerCase().indexOf(filterBy) !== -1)
  }

  pageTitle:string='Students List';

   _studentsFilter : string;

   get studentsFilter() : string {
    return this._studentsFilter;
  }
   set studentsFilter(v : string) {
    this._studentsFilter = v;
    this.filteredStudents=this.studentsFilter ? this.filterRecords(this.studentsFilter) : this.students;
  }
  

  filteredStudents:any[];

  students:any[];
  
    constructor(private studentService:StudentService) {
     }

  ngOnInit():void {
   this.studentService.getStudents().subscribe(
     students => {
    if (localStorage.getItem("Students") === null) {
      localStorage.setItem("Students", JSON.stringify(students));
    }
    else{
      this.students= JSON.parse(localStorage.getItem("Students"));      
    }
    this.filteredStudents = this.students;         
     }  
   );  
  }

  deletStudent(id:number):void{
    let i =0;
    this.students.filter(tempStudent => {if(tempStudent.id === id){
     this.students.splice(i,1);
     localStorage.setItem("Students", JSON.stringify(this.students));
     this.filteredStudents = this.students;
    }
    else{
      i ++;    
  }
  return;
 }
      );
  }

  filterChanged(selectedValue:string):void{
    this.filteredStudents=this.students;
    let tempfilteredStudents:any[]=[];
    if(selectedValue !== "All"){
       let tempfilteredStudents1 = this.filteredStudents.map(tempStudent =>{
        if(tempStudent.type === selectedValue.toLowerCase()){
          tempfilteredStudents.push(tempStudent);
        }
      });
    this.filteredStudents=tempfilteredStudents;

    }
    else{
      this.filteredStudents=this.students;

    }
   
  }

}
