import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StudentService } from '../services/student.service';

@Component({
  selector: 'app-student-edit-view',
  templateUrl: './student-edit-view.component.html',
  styleUrls: ['./student-edit-view.component.css']
})
export class StudentEditViewComponent implements OnInit {
  studentFound:any;
   showDomestic:boolean ;
  constructor(private route:ActivatedRoute,private router:Router,private studentService:StudentService) { }
  docTypes:any[];

  ngOnInit() {
    let studentId =+ this.route.snapshot.paramMap.get('id');
    var students:any[];
    this.studentService.getDocumentTypes().subscribe(
      docs => {this.docTypes=docs;
      console.log(this.docTypes);  
      }
    );
    students= JSON.parse(localStorage.getItem("Students"));

    students.filter(tempStudent => {if(tempStudent.id === studentId){
      this.studentFound = tempStudent;
     }});      
  }

  onBack():void{
    this.router.navigate(['/studentslist']);
  }


}
